import React from 'react'
import Img from '../assets/icons/sayur-box-logo.svg'
import { withRouter } from 'react-router';

const LandingPage = () => {
    return (
        <div className='h-screen d-flex w-100 align-items-center d-flex justify-content-center'>
            <img className='w-25' src={Img} alt='Sayurbox-icon' />
        </div>
    )
}

export default withRouter(LandingPage)